<template>
    <div :class="[activeClass,'box']">
        <header>
            <p @click="clickTop">首页</p>
            <span>{{thisTitle}}</span>
        </header>
        <nav>
            <ul>
                <li @click="clickHandle(item)" v-for="item in navList"> {{item.title}}</li>
            </ul>
        </nav>
    </div>
</template>

<script>
export default {
    props:["activeObj"],
    computed: {
        activeObj2(){
            return this.activeObj.title
        }
    },
    watch: {
       activeObj2(){
        this.activeClass = this.activeObj.navName;
        this.thisTitle = this.activeObj.title;
       }
    },
    data(){
        return{
            activeClass:"movie",
            thisTitle:"电影",
            navList:[
                {
                    title:"电影",
                    pathName:"/",
                    navClass:"movie"
                },
                {
                    title:"书籍",
                    pathName:"/book",
                    navClass:"book"
                },
                {
                    title:"音乐",
                    pathName:"/music",
                    navClass:"music"
                },
                {
                    title:"聊天",
                    pathName:"/chat",
                    navClass:"chat"
                }
            ]
        }
    },
    methods: {
        clickHandle(obj){
            this.$router.push(obj.pathName);
            this.thisTitle = obj.title;
            this.activeClass = obj.navClass;
        },
        clickTop(){
            this.$router.push("/");
        }
    },

}
</script>

<style scoped>

.box header{
    position:fixed;
    top:0;
    height: 1rem;
    width:100%;
    font-size:0.8rem;
    background-color: rgb(33, 150, 243);
    position:relative;
    text-align: center;
    color:white;
}
.box nav{
    position:fixed;
    bottom:0;
    height: 1rem;
    width:100%;
    color:white;
    font-size:0.5rem;
    background-color: rgb(33, 150, 243);
}

.box header p{
    position:absolute;
    font-size:.2rem;
    top:50%;
    left     : 10px;
    text-align : center;
    line-height: 0.2rem;
    transform: translateY(-50%);
}
.box nav ul li{
    flex-grow  : 1;
    text-align : center;
    line-height: 1rem;
}
.box nav ul{
    width  : 100%;
    display: flex;
}

.movie header,.movie nav{
background-color: rgb(33, 150, 243);
}
.music header,.music nav{
background-color: rgb(0, 150, 136);
}
.book header,.book nav{
background-color: rgb(121, 85, 72);
}
.chat header,.chat nav{
background-color: rgb(63, 81, 181);
}
</style>